import { Component, _decorator } from "cc";
import { sp } from "cc";
import { Layout } from "cc";
import { tween } from "cc";
import { Node } from "cc";
import { instantiate } from "cc";
import { Vec3 } from "cc";
import { TweenSystem } from "cc";
import { Button } from "cc";
import { UITransform } from "cc";
import { tigerDetail } from "./tigerDetail";
import { tiger } from "./cTiger";
import { EventTouch } from "cc";

const ROLENUM = 15; //每一轮的角色数量
const TIMEMIN = 0.7; //第一轮摇奖时间
var W_INTERVAL = 0.1;
const FIRSTROLE = [//进入游戏时每列的角色
    [5, 3, 6, 6, 3],
    [4, 7, 7, 7, 2],
    [3, 5, 5, 2, 1],
];
const { ccclass, property } = _decorator;
// 视图层实体是空的
@ccclass('tigerWheel')
export class tigerWheel extends Component {
    @property
    public wheelId: number = 0;
    @property
    public excludeId: number = 0;
    /**0停止 1转 */
    public status: number = 0;
    private nextCb: any;
    public rolePbList: any[] = [];
    public roleIdList: any[] = [];
    public lastResult: any[] = [];
    onLoad() {
        // this.audio = cc.director.getScene().getChildByName('Canvas').getComponent('fortunetigerAudio');
        tiger.wheelList[this.wheelId] = this;
        this.status = 0;
        this.nextCb = null;//下一步需要执行的回调
        this.rolePbList = []; //roles
        this.roleIdList = []; //role ID
    }

    start() {
      
    }

    //初始化场景role
    initWheel() {
        if (tiger.isNewUser) {
            this.lastResult = FIRSTROLE[this.wheelId]; //中间三位是上一局结果 首位末尾是为了防止露出部分转起来不一样 
        } else {
            this.lastResult = [0, 0, 0, 0, 0];
        }

        this.rolePbList = [];
        this.roleIdList = [];
        this.node.removeAllChildren();
        for (let i in this.lastResult) {
            if (tiger.isNewUser) {
                this.addRole(this.lastResult[i]);
            } else {
                this.lastResult[i] = this.getRandomId();
                this.addRole(this.lastResult[i]);
            }
        }
        setTimeout(() => {
            for (let i in this.rolePbList) {
                if (this.roleIdList[i] == 7) {
                    this.rolePbList[i].children[0].getComponent(sp.Skeleton).setAnimation(0, "idle", true);
                } else {
                    //this.rolePbList[i].children[0].getComponent(sp.Skeleton).setAnimation(0, "win_idle", true);
                }
            }
        }, 100);
        this.addRole(this.getRandomId());
        this.node.y = 0;
        tiger.rollIndex++;
        tiger.node.closeShine();
    }

    //清空卷轴
    removeWheel() {
        this.rolePbList = [];
        this.roleIdList = [];
        this.node.removeAllChildren();
        this.node.y = 0;
    }
    private isBlur: boolean = false;
    startRoll(...args: any) {
        var delay = 0;
        this.scheduleOnce(() => {
            if (tiger.slotCtrl.isSpeed) {
                for (let i in args) {
                    this.addRole(args[i]);
                }
                this.addRole(this.getRandomId());
                (this.node.getComponent(Layout) as Layout).updateLayout();
                setTimeout(() => {
                    this.stopImmediately();
                }, TIMEMIN / 3 * 1000);
            }
            let cb = () => {
                this.addRole(this.getRandomId());
                let timeNum = TIMEMIN * 0.68;
                if (this.wheelId == 1) {
                    this.addRole(this.getRandomId());
                    this.addRole(this.getRandomId());
                    this.addRole(this.getRandomId());
                    timeNum += 0.2;
                }
                if (this.wheelId == 2) {
                    this.addRole(this.getRandomId());
                    this.addRole(this.getRandomId());
                    this.addRole(this.getRandomId());
                    this.addRole(this.getRandomId());
                    this.addRole(this.getRandomId());
                    this.addRole(this.getRandomId());
                    timeNum += 0.4;
                }
                for (let i in args) {
                    this.addRole(args[i]);
                }
                this.addRole(this.getRandomId());
                (this.node.getComponent(Layout) as Layout).updateLayout();
                tween(this.node)
                    .to(timeNum, { y: (-this.node.size.height + 842 - 30) })//, { easing: 'quadOut' }
                    .call(() => {
                        this.isBlur = false;
                        this.setBlur();
                    })
                    .to(0.3, { y: (-this.node.size.height + 842) })
                    .call(() => {
                        this.rollCallBack();
                    })
                    .start()
            };
            this.nextCb = cb;
            TweenSystem.instance.ActionManager.removeAllActionsFromTarget(this.node);
            this.nextCb();
        }, delay)
    }

    startFreeRoll(...args: any) {
        let cb = () => {
            this.status = 1;
            this.rolePbList = [];
            this.roleIdList = [];
            this.node.removeAllChildren();
            this.node.y = 0; //滚轮启动位移距离
            for (let i in this.lastResult) {
                this.lastResult[i] = this.lastResult[i] == 0 ? this.getRandomId() : this.lastResult[i];
                this.addRole(this.lastResult[i]);
            }
            for (let i = 0; i < (ROLENUM - 5) * 3; i++) {
                this.addRole(this.getRandomId());
            }


            ((this.node as Node).getComponent(Layout) as Layout).updateLayout()
            let timer = TIMEMIN * 3 // + this.wheelId * 0.2;
            tween(this.node)
                .to(timer, { position: new Vec3(this.node.x, -this.node.size.height + 842, 0) })
                .call(() => {
                    this.FreeRoll_again(args, true)
                    this.isBlur = false
                    this.setBlur()
                })
                .start()
        };
        this.nextCb = cb;
    }

    FreeRoll_again(args: any, isFirst = false) {
        this.status = 1;
        this.rolePbList = [];
        this.roleIdList = [];
        this.node.removeAllChildren();
        this.node.y = 0; //滚轮启动位移距离
        for (let i in this.lastResult) {
            if (!isFirst && parseInt(i) < 4) {
                this.addRole(8).scale = new Vec3(0.85, 0.85, 0.85)
                continue;
            }
            this.lastResult[i] = this.lastResult[i] == 0 ? this.getRandomId() : this.lastResult[i];
            this.addRole(this.lastResult[i]).scale = new Vec3(0.85, 0.85, 0.85)
        }
        let chooseNum = tiger.lotteryRes.getBigWin.card;
        for (let i = 0; i < ROLENUM; i++) {
            let a = this.getRandomId();
            if (a == chooseNum || a == 7) {
                this.addRole(a).scale = new Vec3(0.85, 0.85, 0.85)
            } else {
                this.addRole(8).scale = new Vec3(0.85, 0.85, 0.85)
            }
        }

        for (let i in args) {
            var obj = this.addRole(args[i])
            obj.scale = new Vec3(0.85, 0.85, 0.85)
            if (!isFirst) {
                if (args[i] == this.lastResult[parseInt(i) + 1]) {
                    obj.opacity = 0
                    continue;
                }
            }
        }
        this.addRole(this.getRandomId()).scale = new Vec3(0.85, 0.85, 0.85);
        (this.node.getComponent(Layout) as Layout).updateLayout()
        var delay = isFirst ? 0 : 2
        let timer = TIMEMIN * 1.9
        tween(this.node)
            .delay(delay)
            .call(() => {
                this.isBlur = true
                this.setBlur()
            })
            .to(timer, { position: new Vec3(this.node.x, -this.node.size.height + 842) }, { easing: 'quadOut' })
            .delay(0.8)
            .call(() => {
                this.rollCallBack_Free()
            })
            .start()
        tween(this.node)
            .delay(delay + timer * 2 / 3)
            .call(() => {
                this.isBlur = false
                this.setBlur()
            })
            .start()
    }

    rollLoop() {
        this.status = 1;
        this.rolePbList = [];
        this.roleIdList = [];
        this.node.removeAllChildren();
        this.node.y = 0; //滚轮启动位移距离
        for (let i in this.lastResult) {
            this.lastResult[i] = this.lastResult[i] == 0 ? this.getRandomId() : this.lastResult[i];
            this.addRole(this.lastResult[i]);
        }
        for (let i = 0; i < ROLENUM - 10; i++) {
            this.addRole(this.getRandomId());
        }
        for (let i in this.lastResult) {
            this.lastResult[i] = this.lastResult[i] == 0 ? this.getRandomId() : this.lastResult[i];
            this.addRole(this.lastResult[i]);
        }
        let loopCb = () => {
            this.node.y = 0; //滚轮启动位移距离
            if (tiger.slotCtrl.isSpeed) {
                tween(this.node)
                    .call(() => {
                        this.isBlur = true;
                        this.setBlur();
                    })
                    .to(TIMEMIN / 3, { y: -this.node.size.height + 842 })//, { easing: 'quadInOut' })
                    .call(() => {
                        this.nextCb()
                    })
                    .start()
            } else {
                tween(this.node)
                    .call(() => {
                        this.isBlur = true;
                        this.setBlur();
                    })
                    .to(TIMEMIN, { y: -this.node.size.height + 842 })//, { easing: 'quadInOut' })
                    .call(() => {
                        this.nextCb()
                    })
                    .start()
            }

            // this.node.runAction(cc.repeat(
            //     cc.sequence(
            //         cc.delayTime(0.1),
            //         cc.callFunc(() => {
            //             this.audio.playwheelspin();
            //         })
            //     ), TIMEMIN / 40));
        };

        (this.node.getComponent(Layout) as Layout).updateLayout()

        // this.node.runAction(cc.repeat(
        //     cc.sequence(
        //         cc.delayTime(0.1),
        //         cc.callFunc(() => {
        //             this.audio.playwheelspin();
        //         })
        //     ), TIMEMIN / 0.1));

        if (tiger.slotCtrl.isSpeed) {
            tween(this.node)
                .to(0.16, { y: 30 })
                .call(() => {
                    this.isBlur = true;
                    this.setBlur();
                })
                .to(TIMEMIN * 3 / 4, { y: -this.node.size.height + 842 })
                .call(() => {
                    this.nextCb()
                })
                .start()
        } else {
            tween(this.node)
                .delay(this.wheelId * W_INTERVAL)
                .to(0.16, { y: 30 })
                .call(() => {
                    this.isBlur = true;
                    this.setBlur();
                })
                .to(TIMEMIN, { y: -this.node.size.height + 842 })
                .call(() => {
                    this.nextCb()
                })
                .start()
        }

        // this.node.runAction(cc.repeat(
        //     cc.sequence(
        //         cc.delayTime(0.1),
        //         cc.callFunc(() => {
        //             this.audio.playwheelspin();
        //         })
        //     ), TIMEMIN / 0.1));
        this.nextCb = loopCb;
    }

    getRandomId() {
        let randomId = 0;
        while (randomId == 0) {
            randomId = Math.floor(Math.random() * (tiger.node.rolePb.length - 1) + 1);
            randomId = randomId == this.excludeId ? 0 : randomId;
        };
        return randomId;
    }

    addRole(id: number) {
        if (id == 8) {
            return this.addNullRole(id);
        }
        let pb = instantiate(tiger.node.rolePb[id]);
        this.rolePbList.push(pb);
        this.roleIdList.push(id);
        this.addClickEvent(pb, id);
        this.node.addChild(pb);
        this.setBlur(pb)
        return pb
    }

    addNullRole(id: number) {
        let pb = instantiate(tiger.node.nullPb);
        this.rolePbList.push(pb);
        this.roleIdList.push(id);
        this.node.addChild(pb);
        return pb
    }

    rollCallBack() {
        this.isBlur = false;
        this.setBlur();
        // this.audio.playStopWheel();
        this.lastResult = this.roleIdList.slice(-5);
        this.status = 0;
        tiger.node.stateCallBack();
    }

    rollCallBack_Free() {
        for (var obj of this.node.children) {
            obj.opacity = 255
            tween(obj)
                .delay(0.6 - W_INTERVAL * this.wheelId)
                .to(0.1, { scale: new Vec3(1, 1, 1) })
                .start()
        }
        this.isBlur = false;
        this.setBlur();
        // this.audio.playStopWheel();
        this.lastResult = this.roleIdList.slice(-5);
        this.scheduleOnce(() => {
            this.status = 0;
            tiger.node.stateCallBack();
        }, 0.15)
    }


    stopImmediately() {
        this.isBlur = false;
        this.setBlur();
        this.lastResult = this.roleIdList.slice(-5);
        TweenSystem.instance.ActionManager.removeAllActionsFromTarget(this.node);
        tween(this.node)
            .to(0.05, { y: -this.node.size.height + 842 })
            .start()

        this.status = 0;
        tiger.node.stateCallBack();
    }

    addClickEvent(node: Node, idx: number) {
        let clickEventHandler = new Component.EventHandler();
        clickEventHandler.target = this.node; // 这个 node 节点是你的事件处理代码组件所属的节点
        clickEventHandler.component = "tigerWheel";// 这个是代码文件名
        clickEventHandler.handler = "roleClick";
        clickEventHandler.customEventData = idx+"";

        let button = node.getComponent(Button) as Button;
        button.clickEvents.push(clickEventHandler);
    }

    roleClick(event: EventTouch, customEventData: string) {
        if (tiger.node.effectAllCard.active) {
            return;
        }
        if(tiger.detail){
            tiger.node.deskMask.active = false;
            tiger.detail.node.parent = null;
            tiger.detail = null;
            return;
        }
        let newNode = instantiate(tiger.node.detailPb);
        let transForm:UITransform = (event.target as Node).getComponent(UITransform) as UITransform;
        let newVec3:Vec3 = transForm.convertToWorldSpaceAR(new Vec3(0, 0, 0));
        transForm = tiger.node.getComponent(UITransform) as UITransform;
        let p = transForm.convertToNodeSpaceAR(newVec3);
        newNode.position = p;
        newNode.x -= 50;
        let isLeft = newNode.x < 10;
        (newNode.getComponent("tigerDetail") as tigerDetail).init(Number(customEventData), isLeft);
        tiger.node.node.addChild(newNode);
        tiger.node.deskMask.active = true;
        // cc.log(newNode.position);

    }

    setBlur(node: Node | null = null) {
        // if (this.isBlur) {
        //     return;
        // }
        if (node) {
            this.setNodeBlur(node)
        } else {
            for (var i in this.node.children) {
                let node = this.node.children[i]
                this.setNodeBlur(node);
            }
        }
    }

    setNodeBlur(node: Node) {
        try {
            node.children[0].active = true;
            node.children[1].active = true;
            if (this.isBlur) {
                node.children[0].opacity = 0;
                node.children[1].opacity = 255;
            } else {
                node.children[0].opacity = 255;
                node.children[1].opacity = 0;
            }
        } catch (e) {
            //console.log(e)
        }
    }

}
