import { Vec3 } from "cc";
import { Component } from "cc";
import { _decorator } from "cc";
import { Num } from "../../core_tgx/framework/util/Num";
import { CCFloat } from "cc";

const { ccclass, property } = _decorator;
@ccclass('flyNum')
export class flyNum extends Component {
    @property({ type: CCFloat, tooltip: "飞行进度" })
    private _t: number = 0;
   
    public p0: Vec3 = new Vec3();
 
    public p1: Vec3 = new Vec3();
  
    public p2: Vec3 = new Vec3();
  
    public p3: Vec3 = new Vec3();

    
    public get t() : number {
        return this._t; 
    }
    
    public set t(v : number) {
        this._t = v;
        let temp:Vec3 = Num.threeBezier(this.p0,this.p1,this.p2,this.p3,this._t);
        this.node.position = temp;
    }
}